.search-area {

    &__title {
        text-align: left;
    }

    &__container {
        margin-top: 24px;
    }

    &__metro {}

    &__items {
        margin-top: 32px;
        padding-bottom: 72px;
        grid-column: 1/2;
    }

    &__top {
        grid-column: 1/2;
        grid-row: 1/2;
        align-items: center;
        gap: 8px;
        text-align: left;
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(230px, auto));

        .select-secondary {
            &:nth-child(2) {


                .choices__list--single {
                    padding-left: 75px;
                }
            }

            &:nth-child(3) {


                .choices__list--single {
                    padding-left: 60px;
                }
            }
        }
    }

    &__search {}

    &__map {
        grid-row: 1/3;
        grid-column: 2/3;
        max-height: var(--vh);
        overflow: hidden;
        padding-left: 20px;
        margin-left: -20px;

        &._sticky {
            position: sticky;
            top: 0;
            width: calc(100% + 20px);
        }

        .map-primary {
            height: 100%;
            width: 100%;
        }
    }

    &__resize {
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);


        background-color: var(--white);
        box-shadow: var(--shadow-secondary);
        border-radius: 50%;

        cursor: ew-resize;
        display: flex;
        justify-content: center;
        align-items: center;

        height: 40px;
        width: 40px;
        z-index: 99;

        svg {
            width: 25px;
            height: 25px;
            fill: var(--dark);
        }
    }

    &__controls {
        margin-top: 24px;
        display: flex;
        align-items: center;
        justify-content: end;
    }

    &__control {
        height: 25px;
        padding: 0 16px;
        font-size: 10px;

        &:not(:last-child) {
            margin-right: 16px;
        }
    }

    &__item {
        text-align: left;
        padding: 24px 0;

        &:first-child {
            padding-top: 0;
        }

        &:last-child {
            padding-bottom: 0;
        }

        &:not(:last-child) {
            border-bottom: 1px solid var(--primary200);
        }

        .spollers__title {
            display: flex;
            align-items: center;
            font-size: 16px;
            line-height: math.div(21, 16);
            color: var(--primaryTwo);
            font-weight: 500;
            transition: color $transitionSpeed $transitionFunction;

            >div {
                width: 26px;
                height: 16px;
                display: block;
                margin-right: 12px;
                position: relative;

                span {
                    color: var(--white);
                    font-size: 9px;
                    line-height: 100%;
                    position: absolute;
                    left: 0;
                    top: 1px;


                    background-color: transparent;
                    height: 17px;
                    width: 28px;
                    padding-right: 10px;

                    display: flex;
                    align-items: center;
                    justify-content: center;
                }

                svg {
                    width: 28px;
                    height: 17px;
                    fill: orange;
                }
            }

            >svg {
                width: 20px;
                height: 20px;
                fill: currentColor;
                margin-left: 8px;
                transition: transform $transitionSpeedFast $transitionFunction;
            }

            &:hover {
                color: var(--blue);
            }

            &.focus-visible {
                color: var(--blue);
            }

            &._spoller-active {
                >svg {
                    transform: rotate(-180deg);
                }
            }
        }

        &[data-search-area-color="red"] {
            .spollers__title>div svg {
                fill: var(--red);
            }

            .checkbox-secondary__circle {
                background-color: var(--red);
            }
        }

        &[data-search-area-color="green"] {
            .spollers__title>div svg {
                fill: var(--green);
            }

            .checkbox-secondary__circle {
                background-color: var(--green);
            }
        }

        &[data-search-area-color="dark-blue"] {
            .spollers__title>div svg {
                fill: var(--blue);
            }

            .checkbox-secondary__circle {
                background-color: var(--blue);
            }
        }

        &[data-search-area-color="blue"] {
            .spollers__title>div svg {
                fill: var(--primary400);
            }

            .checkbox-secondary__circle {
                background-color: var(--primary400);
            }
        }

        &[data-search-area-color="brown"] {
            .spollers__title>div svg {
                fill: rgb(145, 81, 51);
            }

            .checkbox-secondary__circle {
                background-color: rgb(145, 81, 51);
            }
        }

        &[data-search-area-color="orange"] {
            .spollers__title>div svg {
                fill: var(--orange)
            }

            .checkbox-secondary__circle {
                background-color: var(--orange)
            }
        }

        &[data-search-area-color="purple"] {
            .spollers__title>div svg {
                fill: rgb(148, 62, 144);
            }

            .checkbox-secondary__circle {
                background-color: rgb(148, 62, 144);
            }
        }

        &[data-search-area-color="yellow"] {
            .spollers__title>div svg {
                fill: var(--yellow);
            }

            .checkbox-secondary__circle {
                background-color: var(--yellow);
            }
        }

        &[data-search-area-color="gray"] {
            .spollers__title>div svg {
                fill: rgb(173, 172, 172);
            }

            .checkbox-secondary__circle {
                background-color: rgb(173, 172, 172);
            }
        }

        &[data-search-area-color="dark-yellow"] {
            .spollers__title>div svg {
                fill: rgb(190, 209, 44);
            }

            .checkbox-secondary__circle {
                background-color: rgb(190, 209, 44);
            }
        }

        &[data-search-area-color="turquoise"] {
            .spollers__title>div svg {
                fill: rgb(91, 190, 187);
            }

            .checkbox-secondary__circle {
                background-color: rgb(91, 190, 187);
            }
        }

        &[data-search-area-color="silver"] {
            .spollers__title>div svg {
                fill: rgb(186, 200, 232);
            }

            .checkbox-secondary__circle {
                background-color: rgb(186, 200, 232);
            }
        }

        &[data-search-area-color="pink"] {
            .spollers__title>div svg {
                fill: rgb(249, 188, 209);
            }

            .checkbox-secondary__circle {
                background-color: rgb(249, 188, 209);
            }
        }

        &[data-search-area-color="dark-pink"] {
            .spollers__title>div svg {
                fill: rgb(222, 100, 161);
            }

            .checkbox-secondary__circle {
                background-color: rgb(222, 100, 161);
            }
        }

    }

    .spollers__body {
        margin-top: 16px;
    }

    &__list {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
        grid-column-gap: 24px;
        grid-row-gap: 24px;
        justify-content: start;
        align-items: start;

        .checkbox-secondary {
            &__label {
                justify-content: start;
            }

            &__text {
                margin-left: 8px;
                display: flex;
                align-items: center;
            }

            &__circle {
                width: 8px;
                height: 8px;
                display: block;
                background-color: orange;
                border-radius: 50%;
                margin-right: 8px;
            }
        }
    }

    .tabs-primary {
        padding: 0;

        &__inner {
            margin-top: 0;
        }

        &__btns {
            justify-content: start;
        }

        &__content {
            margin-top: 0;
        }

        &__container {
            margin-top: 16px;
            background-color: transparent;
            border-radius: 0;
            padding: 0;
        }

        .tabs__title {
            width: auto;
            padding: 5px 12px;

            &:first-child {
                padding-left: 0;
            }
        }
    }

    &__form {
        position: relative;
        display: grid;
        grid-template-columns: 750px 1fr;
        grid-column-gap: 24px;
    }

    &__bottom {
        position: fixed;
        bottom: 0;
        right: 0;
        left: 0;
        background-color: var(--white);
        box-shadow: 0px -2px 1px rgba(0, 0, 0, 0.1);
        padding: 12px 0;

        >div {
            display: flex;
            align-items: center;
            justify-content: end;
            max-width: 1112px;
            padding: 0;
            margin: 0 auto;

            @media (max-width:1112px) {
                padding: 0 16px;
            }
        }
    }

    &__go {
        padding: 0 37px;
        margin-right: 16px;
    }

    @media (max-width:$desktop) {
        padding: 0;
    }
}
